import React from 'react';
import AddStockForm from './AddStockForm';
import StockHeader from './StockHeader';
import { currencies } from './Currencies';
import CSSTransitionGroup from 'react-addons-css-transition-group';

class StocksList extends React.Component {

handleChange = (e,key) => {
  const stock = this.props.stocks[key];
  const updatedStock = {
    ...stock,
    [e.target.name]: e.target.value
  }
  this.props.updateStock(key, updatedStock);
};

 renderCategories = (key) => {
   const category = this.props.categories[key];
   if ( category && category.uid === this.props.uid) {
     return (
       <option key={key} value={key}>
        {category.name}
       </option>
     )
 }
 else {return;}
}

  renderStocksList = (key) => {
    const stock = this.props.stocks[key];
    if ( stock && stock.uid === this.props.uid) {
    return(
      <div className="stock-edit" key={key}>
        <input type="text" name="name" value={stock.name} placeholder="Stock Name"
          onChange={(e) => this.handleChange(e, key)}/>
          <input type="number" name="amount" value={stock.amount}
          onChange={(e) => this.handleChange(e, key)} placeholder="Stock Amount"/>
          <select name="currency" onChange={(e) => this.handleChange(e, key)}
            value={stock.currency}>
            {Object.keys(currencies).map((key) => {return (<option value={key} key={key}> {key}</option>)
            })}
          </select>
          <select name="category" value={stock.category} placeholder="Stock Category"
            onChange={(e) => this.handleChange(e, key)}>
            {Object.keys(this.props.categories).map(this.renderCategories)}
          </select>
          <span>{parseInt(100 * this.props.stockPrice(stock), 10) / 100}</span>
          <span>{this.props.stockValue(stock)}</span>
          <span>{this.props.userTotal ? parseInt(1000 * this.props.stockValue(stock) / this.props.userTotal, 10) / 10 : 0}%</span>
        <button className="deleteButton" onClick={() => this.props.removeStock(key)}> &times;</button>
      </div>
    )}
    else {return;}
  };

  render(){
    return(
      <div>
        <h2>Stocks List</h2>
        <StockHeader />
        <CSSTransitionGroup
          component="div"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={500}
          transitionName="item">
          {Object.keys(this.props.stocks).map(this.renderStocksList)}
        </CSSTransitionGroup>
        Total: {this.props.userTotal.toFixed(2)} {this.props.currency[this.props.uid]}
        {this.props.notification}
        <AddStockForm
          addStock={this.props.addStock}
          categories={this.props.categories}
          renderCategories={this.renderCategories}
          uid={this.props.uid}
        />

      </div>
    )
  }
  static propTypes = {
    stocks: React.PropTypes.object.isRequired,
    removeStock: React.PropTypes.func.isRequired,
    updateStock: React.PropTypes.func.isRequired,
    addStock: React.PropTypes.func.isRequired,
    uid: React.PropTypes.string,

  };
}

export default StocksList;



