import React from 'react';
import { currencies } from './Currencies';

class AddStockForm extends React.Component {
  createStock(event){
    event.preventDefault();
    const stock = {
      name: this.name.value,
      category: this.category.value,
      amount: parseFloat(this.amount.value),
      currency: this.currency.value,
      uid: this.props.uid,

    }
    this.props.addStock(stock);
    this.stockForm.reset();
  }

  render(){

    return(
      <form ref={(input) => this.stockForm = input} className="stock-edit" onSubmit={(e) => this.createStock(e)}>
      <input ref={(input) => this.name = input} type="text" placeholder="Stock Name" required/>
      <input ref={(input) => this.amount = input} type="number" placeholder="Stock Amount" required/>
      <select ref={(input) => this.currency = input} required>
        {Object.keys(currencies).map((key) => {return (<option value={key} key={key}> {key}</option>)
        })}
      </select>
      <select ref={(input) => this.category = input} required>
      {Object.keys(this.props.categories).map(this.props.renderCategories)}
      </select>
      <button type="submit">+ Add Stock</button>
      </form>
    )
  }

  static propTypes = {
    addStock: React.PropTypes.func.isRequired
  };
}



export default AddStockForm;



