import React from 'react';
import { isEmpty } from './helpers';

class Recommendations extends React.Component {

constructor(props){
  super(props);
  this.fee = {
    USD: 9,
    ILS: 20
  }
}


  categoryDifference = (key) => {
      if (this.props.categories[key]) {
      const categoryValue = this.props.categoriesTotal[key] || 0;
      const categoryPercentage = this.props.userTotal ?
        parseInt(1000 * categoryValue / (this.props.userTotal), 10) / 10 : 0;
        return (parseFloat(this.props.categories[key].percentage) - categoryPercentage);
      };
      return;
  };

  maxDifference = () => {
    let maxKey;
    let max = 0;
       Object.keys(this.props.categories).forEach((key) => {
        if (this.props.categories[key] && this.props.categories[key].uid === this.props.uid &&
           Math.abs(this.categoryDifference(key)) >= max) {
          max = Math.abs(this.categoryDifference(key));
          maxKey = key;
        }});
      return maxKey;
  };

  firstStock = (categoryKey) => {
    const stocks = this.props.stocks;
    let stock = null;
    let changed = false;
    if (!isEmpty(stocks)){
      Object.keys(stocks).forEach(stockKey => {
        if (stocks[stockKey].category === categoryKey && !changed){
          stock = stockKey;
          changed = true;
        }
      });
    };
    return stock;
  };

 notFavoriteStock = (categoryKey) => {
   const stocks = this.props.stocks;
   let stock = this.props.categories[categoryKey].favStock;
   let changed = false;
   if (!isEmpty(stocks)){
     Object.keys(stocks).forEach(stockKey => {
       if (stocks[stockKey].category === categoryKey &&
         stockKey !==this.props.categories[categoryKey].favStock
         && !changed){
         stock = stockKey;
         changed = true;
       }
     });
   };
   return stock;
 };

  userCash(){
    let cash = "";
    const stocks = this.props.stocks;
    if (!isEmpty(stocks)) {
      Object.keys(stocks).forEach(key => {
        if (stocks[key] && stocks[key].uid === this.props.uid && stocks[key].name.toUpperCase() === 'CASH'){
          cash += `${stocks[key].amount} ${stocks[key].currency}; `;
        }
      });
    }
    if (cash.length > 0) {
      cash = cash.slice(0,-2)+".";
    }
    else { cash = 'no cash'}
    return cash;
  };

  oneAction = () => {

    const maxKey = this.maxDifference();
    const stocks = this.props.stocks;

    if (!isEmpty(this.props.categories && maxKey)) {
      const category = this.props.categories[maxKey];
      const initialSum = 0.01 * Math.abs(this.categoryDifference(maxKey)) *
        this.props.userTotal; //sum in user currency
      let action;
      let stock;
      let amount;
      let stockCurrency;
      let stockName;
      let dealSum;

      if (this.categoryDifference(maxKey) > 0){
        action = "Buy";
        if (category.favStock) {
          stock = stocks[category.favStock];
        }
        else if (this.firstStock(maxKey)){
          stock = stocks[this.firstStock(maxKey)];
        }
      }
      else {
        action = "Sell";
        if (this.notFavoriteStock(maxKey)) {
          stock = stocks[this.notFavoriteStock(maxKey)];
        }
      }
        if (stock) {
          stockName = stock.name;
          stockCurrency = stock.currency;
          amount = (action === "Buy") ? Math.floor(initialSum / this.props.stockPrice(stock)) :
          Math.min(Math.floor(initialSum / this.props.stockPrice(stock)), stock.amount);
          dealSum = `${(amount * this.props.prices[stock.name]).toFixed(2) + this.fee[stockCurrency]} of ${stock.currency}`;

        }
        else {
          amount = "some";
          stockName = "stocks";
          dealSum = `${(initialSum ).toFixed(2)} of ${this.props.currency[this.props.uid]} (prepare to pay some fees)`;
        }
      const funds = (action === "Buy"  && initialSum !== 0) ? `You'll need ${dealSum}. Currently you have ${this.userCash()}` : "";

    return (
      <div className="recommendation">
        {action} {amount} units of {stockName} to balance the category {category.name}.
        {funds}
      </div>)
    }
    else {
      return <div className="recommendation"> Define some categories! </div>
    }
  };

  render(){
    return(
      <div className="stock-edit">
        <h5> Recommendation for one action </h5>
        {this.oneAction()}
      </div>
    )
  }
}

export default Recommendations;



