import React from 'react';
import CSSTransitionGroup from 'react-addons-css-transition-group';
import { isEmpty } from './helpers';

class Categories extends React.Component {

  handleChange = (e,key) => {
    const category = this.props.categories[key];
    const updatedCategory = {
      ...category,
      [e.target.name]: e.target.value
    }
    this.props.updateCategory(key, updatedCategory);
  };

  createCategory(event){
    event.preventDefault();

    const category = {
      name: this.name.value,
      percentage: parseFloat(this.percentage.value),
      uid: this.props.uid
    };
    this.props.addCategory(category);
    this.categoryForm.reset();
  }

  isCategoryStock = (categoryKey) => {
    let isFound = false;
    Object.keys(this.props.stocks).forEach((key) => {
      if (this.props.stocks[key] && this.props.stocks[key].category === categoryKey){
        isFound = true;
        return isFound;
      }
    });
    return isFound;
  };

  renderCategoryStock = (stockKey,categoryKey) => {
      if (this.props.stocks[stockKey] && this.props.stocks[stockKey].category === categoryKey){
      return (
        <option value={stockKey} key={`${stockKey}${categoryKey}`}>
          {
            this.props.stocks[stockKey].name}
        </option>
      )}
      else { return; }
  };

  largerThanHundred = () => {
    let sum = 0;
    if (!isEmpty(this.props.categories)){
      Object.keys(this.props.categories).forEach((key) => {
        if (this.props.categories[key] && this.props.categories[key].uid === this.props.uid) {
          sum += parseFloat(this.props.categories[key].percentage);
        }
      });
    }
   return (sum > 100);
  };

  renderCategoriesList = (key) => {
    const category = this.props.categories[key];
    if ( category && category.uid === this.props.uid) {
      const categoryValue = this.props.categoriesTotal[key] || 0;
      const categoryPercentage = this.props.userTotal ?
        parseInt(1000 * categoryValue / this.props.userTotal, 10) / 10 : 0;
    return(
      <div className="category-edit" key={key}>
        <input type="text" name="name" value={category.name} placeholder="Category Name"
          onChange={(e) => this.handleChange(e, key)} required/>
          <span>{categoryValue.toFixed(2)}</span>
          <input type="number" name="percentage" value={category.percentage}
          onChange={(e) => this.handleChange(e, key)} placeholder="Desirable %"/>
          <span>{categoryPercentage}%</span>
          <select name="favStock" value={category.favStock} onChange={(e) => this.handleChange(e, key)}>
          {Object.keys(this.props.stocks).map(stockKey => this.renderCategoryStock(stockKey, key))}
          </select>
        <button className="deleteButton" onClick={() => this.props.removeCategory(key)} disabled={this.isCategoryStock(key)}> &times;</button>
      </div>
    )}
    else {return;}
  };

  render(){
    const notification = (this.largerThanHundred()) ?
    <div className="notification">Summary percentage of your categories is larger than 100.
    Fix it to get better recommendations.</div> : "";
    return (
      <div>
        <h2>Categories</h2>
        <div className="category-edit">
          <span className="heading">Category Name</span>
          <span className="heading">Category Value</span>
          <span className="heading">Desirable %</span>
          <span className="heading">Current %</span>
          <span className="heading">Favourite stock </span>
        </div>
        <CSSTransitionGroup
          component="div"
          transitionName="item"
          transitionEnterTimeout={500}
          transitionLeaveTimeout={500}
        >
          {Object.keys(this.props.categories).map(this.renderCategoriesList)}
        </CSSTransitionGroup>
        {notification}
      <form ref={(input) => this.categoryForm = input} className="category-edit" onSubmit={(e) => this.createCategory(e)}>
      <input ref={(input) => this.name = input} type="text" placeholder="Category Name" required/>
      <input ref={(input) => this.percentage = input} type="number" placeholder="Desirable %" required/>
      <button type="submit">+ Add Category</button>
      </form>
      </div>
    )
  }
}
  export default Categories;



