import React from 'react';

class StockHeader extends React.Component {
  render(){
    return(

      <div className="stock-edit">
        <span className="heading">Stock Name</span>
        <span className="heading">Stock Amount</span>
        <span className="heading">Currency</span>
        <span className="heading">Stock Category</span>
        <span className="heading">Stock Price</span>
        <span className="heading">Stock Value</span>
        <span className="heading">% of Total </span>
      </div>

)
}
}
export default StockHeader;



