import React from 'react';

const Header = (props) =>{
    return(
      <header className="top">

        <h3 className="tagline"> <span>{props.tagline}</span></h3>
      </header>
    )
}

Header.propTypes = {
  tagline : React.PropTypes.string.isRequired
};

export default Header;



